header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}
.header__container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* =============================CTA====================== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ============================Header social====================== */

.header__social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__social::after {
  content: "";
  width: 1px;
  height: 3rem;
  background-color: var(--color-primary);
}

/* ============================Profile Image====================== */

.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}
.me img {
  height: 20rem;
  width: auto;
}

/* ============================scroll down text====================== */
.scroll__down {
  position: absolute;
  right: -2.4rem;
  bottom: 5rem;
  transform: rotate(90deg);
}

/*================== MEDIA QUERIES (MEDIUM DEVICE)=================== */
@media screen and (max-width: 1024px) {
  header {
    padding-top: 2rem;
  }
  .me img {
    height: 20rem;
    width: auto;
  }
}
